@each $size, $value in $responsive-font-sizes {
	@for $i from 1 through 4 {
		.display-#{$i} {
			@include media-breakpoint-up($size) {
				font-size: $value * 1.8 !important;
			}
		}
	}
	
	@for $i from 1 through 6 {
		h#{$i}, .h#{$i} {
			@include media-breakpoint-up($size) {
				font-size: $value + 0.2 - ($i - 1) * 0.1 !important;
			}
		}
	}
	
	span, p, li {
		@include media-breakpoint-up($size) {
			font-size: $value * 0.6 + 0.15 !important;
		}
	}
}
$transition-duration: .5s;
$transition-easing: ease-in-out;
$animation-transition: all $transition-duration $transition-easing;

.slide-fade-header-enter-active, .slide-fade-header-leave-active {
	transition: $animation-transition !important;
	z-index: $aside-index;
}

.slide-fade-header-enter, .slide-fade-header-leave-to {
	@include media-breakpoint-only(xs) {
		transform: translateX(-100%) !important;
	}
	transform: translateY(-100%) !important;
	opacity: 0 !important;
}

//---------------------------------------

.fade-enter-active, .fade-leave-active {
	transition: $animation-transition;
}

.fade-enter, .fade-leave-to {
	opacity: 0;
}

//---------------------------------------

.fade-scale-enter-active, .fade-scale-leave-active {
	transition: $animation-transition;
}

.fade-scale-enter, .fade-scale-leave-to {
	opacity: 0 !important;
	transform: scale(0.9) !important;
}

//---------------------------------------

.fade-slide-horizontal-active, .fade-slide-horizontal-leave-active {
	transition: $animation-transition;
}

.fade-slide-horizontal-enter, .fade-slide-horizontal-leave-to {
	opacity: 0 !important;
	transform: translateX(-100%) !important;
}



















































@font-face {
    font-family: 'Norse';
    font-style: normal;
    font-weight: normal;
    font-display: swap;
    src: url("assets/fonts/Norse.otf");
}

@font-face {
    font-family: 'Futhark';
    font-style: normal;
    font-weight: normal;
    font-display: swap;
    src: url("assets/fonts/BabelStoneRunicElderFuthark.ttf");
}

@font-face {
    font-family: 'Elegant';
    font-style: normal;
    font-weight: normal;
    font-display: swap;
    src: url("assets/fonts/ElegantLux.otf");
}

* {
    box-sizing: border-box;
    @extend .primary-font;
}

html, body {
    margin: 0;
    padding: 0;
    font-size: 30px;
}

#app {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    position: relative;
}

main {
    flex: 1 0 auto;
}

.btn {
    @extend .rounded-0, .font-weight-bold, .bg-transparent, .border, .border-white, .px-4, .py-2;
    border-width: 5px !important;

    &:hover {
        @extend .bg-white, .text-black;
    }

    &:focus {
        box-shadow: none !important;
    }
}

a {
    &:hover {
        text-decoration: none;
    }
}

@for $i from 1 through 6 {
    h#{$i} {
        @extend .font-weight-bold;
    }
}
