@each $size, $value in $responsive-font-sizes {
	@for $i from 1 through 4 {
		.display-#{$i} {
			@include media-breakpoint-up($size) {
				font-size: $value * 1.8 !important;
			}
		}
	}
	
	@for $i from 1 through 6 {
		h#{$i}, .h#{$i} {
			@include media-breakpoint-up($size) {
				font-size: $value + 0.2 - ($i - 1) * 0.1 !important;
			}
		}
	}
	
	span, p, li {
		@include media-breakpoint-up($size) {
			font-size: $value * 0.6 + 0.15 !important;
		}
	}
}
$transition-duration: .5s;
$transition-easing: ease-in-out;
$animation-transition: all $transition-duration $transition-easing;

.slide-fade-header-enter-active, .slide-fade-header-leave-active {
	transition: $animation-transition !important;
	z-index: $aside-index;
}

.slide-fade-header-enter, .slide-fade-header-leave-to {
	@include media-breakpoint-only(xs) {
		transform: translateX(-100%) !important;
	}
	transform: translateY(-100%) !important;
	opacity: 0 !important;
}

//---------------------------------------

.fade-enter-active, .fade-leave-active {
	transition: $animation-transition;
}

.fade-enter, .fade-leave-to {
	opacity: 0;
}

//---------------------------------------

.fade-scale-enter-active, .fade-scale-leave-active {
	transition: $animation-transition;
}

.fade-scale-enter, .fade-scale-leave-to {
	opacity: 0 !important;
	transform: scale(0.9) !important;
}

//---------------------------------------

.fade-slide-horizontal-active, .fade-slide-horizontal-leave-active {
	transition: $animation-transition;
}

.fade-slide-horizontal-enter, .fade-slide-horizontal-leave-to {
	opacity: 0 !important;
	transform: translateX(-100%) !important;
}



























































































#container {
    user-select: none;
    width: 100vw;
    height: 100vh;
    background: transparent;
    transition: $transition-base;
}

#container.active {
    background: transparentize($black, 0.4);
    z-index: $aside-index;

    nav.navbar {
        overflow-y: auto;
    }

    .horizontal {
        transform: translateX(0);
    }
}

nav.navbar div.position-absolute {
    left: 0;
    right: 0;
}

div.position-fixed.d-flex {
    height: 100vh;
    z-index: $aside-index + 1;

    .horizontal {
        transform: translateX(-100%);
        transform-origin: left;
        transition: $transition-base;
    }
}

.menu {
    z-index: $aside-index + 2;
    @include media-breakpoint-down(md) {
        &:hover, &:focus {
            background-color: transparent !important;
            color: $secondary !important;
        }
    }
}

.img-fluid {
    width: 5rem;
    height: auto;
}
